import { UserByTypeInterfaceAPI } from "./user_by_type";

export interface centerReportInterface {
  _id: string;
  occupied: number;
  isUsed: boolean;
  center_id: string;
  room_type: string;
  room_number: number;
  capacity: number;
  center_name: string;
  district_id: string;
  district_name: string;
  course_in_room: courseInRoomInterface[];
}

export interface centerReportInterface_NEW {
  _id: string;
  occupied: number;
  isUsed: boolean;
  center_id: string;
  room_type: string;
  room_number: number;
  capacity: number;
  center_name: string;
  district_id: string;
  district_name: string;
  invigilator: string;
  course_in_room: courseInRoomInterface[];
}

export interface courseInRoomInterface {
  course_id: string;
  course_name: string;
  course_code: string;
  occupied: number;
}

export interface CenterToPrintInterface {
  no: number;
  district_name: string;
  center_name: string;
  room_number: number;
  room_type: string;
  room_capacity: number | "-";
  room_occupation: number;
  invigilator: string;
}

export enum centerReportContentKeyInterface {
  district_name = "district_name",
  center_name = "center_name",
  room_number = "room_number",
  room_type = "room_type",
  room_capacity = "room_capacity",
  room_occupation = "room_occupation",
  invigilator = "invigilator",
}

export interface centerReportContentInterface {
  key: centerReportContentKeyInterface;
  title: string;
  status: boolean;
}

export interface roomDistrictInterface {
  district_id: string;
  district_name: string;
}

export const getDistrictInRoom = (
  allData: centerReportInterface[]
): roomDistrictInterface[] => {
  let result: roomDistrictInterface[] = [],
    ids: string[] = [];
  for (const key in allData) {
    if (Object.prototype.hasOwnProperty.call(allData, key)) {
      if (!ids.includes(allData[key].district_id)) {
        ids.push(allData[key].district_id);
        result.push({
          district_id: allData[key].district_id,
          district_name: allData[key].district_name,
        });
      }
    }
  }

  return result;
};

export const getRoomOccupation = (thisRoom: centerReportInterface): number => {
  let result: number = 0;
  for (const key in thisRoom.course_in_room) {
    if (Object.prototype.hasOwnProperty.call(thisRoom.course_in_room, key)) {
      const element = thisRoom.course_in_room[key];
      if (result < element.occupied) {
        result = element.occupied;
      }
    }
  }
  return result;
};

export const roomWithOccupation = (
  allRoom: centerReportInterface[],
  invigilators: UserByTypeInterfaceAPI[]
): centerReportInterface_NEW[] => {
  let result: centerReportInterface_NEW[] = [];
  for (const key in allRoom) {
    if (Object.prototype.hasOwnProperty.call(allRoom, key)) {
      let data: centerReportInterface = allRoom[key];
      data.occupied = getRoomOccupation(allRoom[key]);
      const invigilator = invigilators.find(
        (item) => item.roles_to_users.room_id === allRoom[key]._id
      );
      result.push({
        _id: data._id,
        occupied: data.occupied,
        isUsed: data.isUsed,
        center_id: data.center_id,
        room_type: data.room_type,
        room_number: data.room_number,
        capacity: data.capacity,
        center_name: data.center_name,
        district_id: data.district_id,
        district_name: data.district_name,
        invigilator: invigilator
          ? invigilator.user_info.fname + " " + invigilator.user_info.lname
          : "",
        course_in_room: data.course_in_room,
      });
    }
  }
  return result;
};

