import React, { useState } from "react";
import { MdSearch } from "react-icons/md";
import { roomDistrictInterface } from "../../../interfaces/center_report";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import search from "../../../utils/search";

type RoleListProps = {
  setChooseDistrict: (value: boolean) => void;
  districtList: roomDistrictInterface[];
  setSelectedDistrict: (data: roomDistrictInterface | "all") => void;
  selectedDistrict: roomDistrictInterface | "all";
  loadindRequest: boolean;
  bgWhite?: boolean;
  isModal?: boolean;
};

const DistrictList: React.FC<RoleListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  return (
    <section
      className={`${
        props.isModal ? "" : "border-2 rounded border-gray-400 shadow-md"
      }`}
    >
      {/* SEARCH PART */}
      <div className="flex items-center w-full my-1">
        <MdSearch className="w-6 h-6 absolute ml-2" />
        <input
          type="text"
          placeholder="Search"
          className={`focus:border-2 h-7 ml-1 pl-9 ${
            props.isModal ? "mr-1" : "mr-2"
          } bg-gray-100 focus:border-gray-700 w-full rounded`}
          onChange={(e) => setSearchInput(e.target.value)}
          value={searchInput}
          disabled={props.districtList.length > 0 ? false : true}
        />
        {!props.isModal && (
          <button
            className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
            type="button"
            onClick={() => {
              props.setChooseDistrict(false);
            }}
          >
            Close
          </button>
        )}
      </div>
      {/* END SEARCH PART ///////////////// */}

      <div
        className={`w-full h-52 mt-2 ${
          props.isModal ? "px-2" : ""
        } overflow-y-auto divide-y divide-gray-300 ${
          props.bgWhite && "bg-white"
        }`}
      >
        <section>
          <div
            className={`group flex items-center justify-between my-1 ${
              props.selectedDistrict === "all"
                ? "cursor-default bg-blue-50 hover:bg-blue-100"
                : "hover:bg-blue-100"
            }`}
          >
            <div
              className={`p-1 w-full mr-2 ${
                props.selectedDistrict === "all"
                  ? "cursor-default"
                  : "cursor-pointer"
              }`}
              onClick={() => {
                props.setChooseDistrict(false);
                !props.loadindRequest &&
                  props.selectedDistrict !== "all" &&
                  props.setSelectedDistrict("all");
              }}
            >
              <p className="text-sm font-medium text-black">All district</p>
            </div>
            <button
              type="button"
              className="bg-blue-200 group-hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 p-1 mr-1 rounded text-sm"
            >
              Choose
            </button>
          </div>
        </section>
        {search(props.districtList, searchInput).length > 0 ? (
          search(props.districtList, searchInput).map((district, index) => (
            <section key={index + district.district_id}>
              <div className="group flex items-center justify-between my-1 hover:bg-gray-100">
                <div
                  className="p-1 w-full mr-2 cursor-pointer"
                  onClick={() => {
                    props.setChooseDistrict(false);
                    !props.loadindRequest &&
                      props.setSelectedDistrict(district);
                  }}
                >
                  <p className="text-sm font-medium text-black">
                    {capitalizeFirstLetter(district.district_name)}
                  </p>
                </div>
                <button
                  type="button"
                  className="bg-blue-200 group-hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 p-1 mr-1 rounded text-sm"
                >
                  Choose
                </button>
              </div>
            </section>
          ))
        ) : (
          <div className="text-center p-3">
            <p className="text-base font-medium text-black">
              No district available
            </p>
          </div>
        )}
      </div>
    </section>
  );
};

export default React.memo(DistrictList);
